@use 'base/variables' as v;
@use 'base/mixins' as m;

.confirmed {
    margin-top: 5rem;
    .uptask {
        @include m.gradientText (v.$indigo);
    }
    .actions {
        justify-content: center;
        a {
            color: v.$indigo;
            font-weight: bold;
        }
    }
}