@use 'base/variables' as v;
@use 'base/mixins' as m;

.login {
    margin-top: 5rem;
    .uptask {
        @include m.gradientText (v.$cyan);
    }
    input[type="submit"] {
        @include m.button (v.$cyan, v.$white)
    }
    .actions {
        a {
            color: v.$cyan;
            font-weight: bold;
        }
    }
}