@use 'base/variables' as v;
@use 'base/mixins' as m;

.alert {
    padding: 1rem 1rem 1rem 2rem;
    text-transform: uppercase;
    font-weight: 900;
    margin: 1rem 0;
    font-size: 1.2rem;
    max-width: 80rem;
    &.error {
        background-color: lighten(v.$red, 60%);
        border-left: .5rem solid v.$red;
        border-radius: .5rem;
        color: v.$red;
    }
    &.success {
        background-color: lighten(v.$green, 60%);
        border-left: .5rem solid v.$green;
        border-radius: .5rem;
        color: v.$green;
    }
}