@use 'base/variables' as v;

/** Media Queries **/
@mixin mobile {
    @media (min-width: v.$mobile) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin button($bgColor, $textColor) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $textColor;
    font-size: 1.6rem;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    display: block;
    width: 100%;
    border-radius: .5rem;
    @include tablet {
        width: auto;
    }
    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }
}

@mixin grid($columns, $spacing ) {
    display: grid;
    gap: $spacing;
    grid-template-columns: repeat($columns, 1fr);
}
@mixin gradientText ($color) {
    //Degradado desde el comienzo al 100%del ancho. Color inicial $color hasta un 30% más claro
    background: linear-gradient(to right, $color,lighten($color, 30%) 100% );
    color: transparent;
    background-clip: text; //Agrega la máscara del texto al fondo. De esta forma el texto toma ese color degradado
    font-size: 7rem;
    text-align: center;
    @include tablet {
        font-size: 10rem;
    }
}