@use 'base/variables' as v;
@use 'base/mixins' as m;

.bar-mobile {
    background-color: v.$indigo;
    padding: 2rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include m.tablet {
        display: none;
    }
    h1 {
        color: v.$white;
        margin: 0;
    }
    .menu {
        img {
            width: 8px;
            &:hover {
                cursor: pointer;
            }
        }
    }
}
.bar {
    display: none;

    @include m.tablet {
        margin: 1rem;
        background-color: v.$white;
        padding: 1rem 2rem;
        border-radius: .5rem;
        box-shadow: 1px 12px 9px -13px rgba(0,0,0,.4);
        display: flex; //Coloca de izquierda a derecha
        justify-content: space-between;
        align-items: center;
    }
    p {
        margin: 0;
        font-size: 1.4rem;
        span {
            font-weight: 700;
            color: v.$indigo;
        }
    }
    .logout {
        @include m.button (darken(v.$indigo, 20%), v.$white);
        font-size: 1.2rem;
        padding: 1rem;
    }
}