@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard {
    background-color: v.$lightGray;

    @include m.tablet {
        display: flex;
        height: 100vh; //!00% del alto y del ancho
    }
    .principal {
        flex: 1; //Fuerza a tomar todo el contenido a lo ancho no lo que requiere
        overflow: scroll;
    }
    .contents {
        padding: 2rem;
        .page-name {
            font-size: 2.8rem;
            text-align: center;
        }
    }
    .form {
        input[type="submit"] {
            @include m.button(v.$indigo, v.$white);
            padding: 1rem; //Lo hace más pequeño
            font-weight: bold;
            font-size: 1.4rem;
        } 
    }
    .link {
        color: v.$indigo;
        font-weight: 900;
        &:hover  {
            color: darken(v.$indigo, 20%);
        }
    }
}