@use 'base/variables' as v;
@use 'base/mixins' as m;

.create {
    margin-top: 5rem;
    .uptask {
        @include m.gradientText (v.$purple);
    }
    input[type="submit"] {
        @include m.button (v.$purple, v.$white)
    }
    .actions {
        a {
            color: v.$purple;
            font-weight: bold;
        }
    }
}