@use 'variables' as v;

*, *:before, *:after {
    box-sizing: inherit;
}
html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    font-family: v.$font-text;
    font-size: 1.6rem;
    min-height: 100%;
}
.container {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
.container-sm {
    max-width: 70rem;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
}
//En .actions cuando se pone el ratón encima de los enlaces se pone el fondo gris claro con transición
.actions {
    a:hover {
        background-color: v.$lightGray;
        border-radius: .5rem;
        transition-property: background-color;
        transition-duration: 300ms;
    }
}