@use 'base/variables' as v;
@use 'base/mixins' as m;

//Modo oscuro, se cambia el color de algunos elementos
@media (prefers-color-scheme: dark) {
    body {
        background-color: v.$darkGray;
    }
    .tagline,
    .form label,
    .login .actions a,
    .create .actions a,
    .forgotten .actions a {
        color: v.$white;
        &:hover {
            color: v.$gray;
        }
    }
    .description-page,
    .form .field input,
    .page-name {
        color: v.$mediumGray;
    }
    .form .field input {
        background-color: lighten(v.$darkGray,10%);
        color: v.$white;
        &::placeholder {
            color: darken(v.$darkGray,15%);
        }
    }
    .alert.error {
        background-color: v.$lightGray;
    }
    .sidebar {
        @include m.tablet {
            background-color: darken(v.$darkGray, 5%);
        }
        .sidebar-nav a {
            &.active,
            &.hover {
                background-color: v.$orange;
            }
        }
    }
    .bar-mobile,
    .bar {
        background-color: darken(v.$darkGray, 5%);
    }
    .bar{
        p {
            color: v.$orange;
            span {
                color: v.$lightGray;
            }
        }
        .logout {
            @include m.button(v.$orange, v.$white);
            padding: .7rem;
            font-size: 1.2rem;
        }
}
    .filters {
        background-color: lighten(v.$darkGray,15%);
        h2,
        label {
            color: v.$white;
        }
        input[type="radio"] {
            accent-color: v.$orange;
        }
    }
    .dashboard {
        background-color: v.$darkGray;
        .link {
            color: v.$lightGray;
            &:hover {
                color: v.$orange;
            }
        }
        .form input:not([type=submit]) {
            background-color: lighten(v.$darkGray,10%);
            color: v.$white;
            &::placeholder {
                color: darken(v.$darkGray,15%);
            }
        }
    }
    .list-projects .project {
        background-color: lighten(v.$darkGray,10%);
        &:hover {
            background-color: lighten(v.$darkGray,5%);
        }
    }
    .tasks-list .task {
        background-color: lighten(v.$darkGray,10%);
        border: none;
        p {
            color: v.$lightGray;
        }
    }
    .modal .form {
        background-color: v.$darkGray;
        input[type="text"] {
            background-color: lighten(v.$darkGray,10%);
            border: none;
            color: v.$white;
            &::placeholder {
                color: darken(v.$darkGray,15%);
            }
        }
        legend {
            color: v.$lightGray;
        }
    }
}