@use 'base/variables' as v;
@use 'base/mixins' as m;

.container-new-task {
    display: flex;
    justify-content: center;

    .add-task {
        @include m.button (v.$cyan, v.$white);
        .button {
            border-radius: .5rem;
        }
    }
}
.tasks-list {
    list-style: none;
    padding: 0;
    margin-top: 3rem;
    font-weight: bold;

    .no-tasks {
        text-align: center;
    }
}
.task {
    padding: .5rem 2rem;
    border-bottom: 2px solid v.$mediumGray;
    margin-bottom: .5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &:last-of-type {
        border: none;
    }
    p {
        font-size: 1.4rem;
    }
    .options {
        display: flex;
        gap: 1rem;
        button {
            border: none;
            font-size: .9rem;
            padding: .5rem 1rem;
            text-transform: uppercase;
            font-weight: 700;
            border-radius: 1rem;
            &:hover {
                cursor: pointer;
            }
        }
        .completed {
            background-color: v.$blue;
            color: v.$white;
        }
        .pending {
            background-color: v.$orange;
            color: v.$white;
        }
        .delete-task {
            background-color: v.$red;
            color: v.$white;
        }

    }

}