@use 'base/variables' as v;
@use 'base/mixins' as m;

.no-projects {

    a {
        color: v.$indigo;
    }
}

.list-projects {

    list-style: none;
    padding: 0;
    margin-top: 3rem;
    @include m.grid(1,2rem);
    @include m.tablet {
        @include m.grid(2,2rem);
    }
    @include m.desktop {
        @include m.grid(3,2rem);
    }
    a {
        color: v.$white;
    }

    .project {
        background-color: lighten(v.$indigo, 5%);
        min-height: 12rem;
        display: flex;
        align-items: center;
        padding: 2rem;
        border-radius: .5rem;
        line-height: 2;

        &:hover {
            background-color: darken(v.$indigo, 5%);
        }
    }
}