@use 'base/variables' as v;
@use 'base/mixins' as m;

.forgotten {
    margin-top: 5rem;
    .uptask {
        @include m.gradientText (v.$pink);
    }
    input[type="submit"] {
        @include m.button (v.$pink, v.$white)
    }
    .actions {
        a {
            color: v.$pink;
            font-weight: bold;
        }
    }
}